
import { watchEffect, defineComponent } from 'vue';
import store from '../store'
import axios from 'axios'
import gsap from 'gsap'

export default defineComponent({
    name: 'Reviews',
    setup() {
        return {store}
    },
    mounted() {
         watchEffect(() => {
           if (!store.state.book.reviews?.length) return
         })
    }
})
